import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import HomPage from "./route/homepage";
import About from "./route/about";
import Contact from "./route/contact";
import { Button, Grid } from "@material-ui/core";

function App() {
  return (
    <Router>
      <Grid container>
        <Grid item container justify="space-around" xs={6}>
          <Button variant="contained" color="primary" component={Link} to={"/"}>
            Home
          </Button>
          <Button
            variant="contained"
            color="primary"
            component={Link}
            to={"/contact"}
          >
            Contact
          </Button>
          <Button
            variant="contained"
            color="primary"
            component={Link}
            to={"/about"}
          >
            About
          </Button>
        </Grid>
      </Grid>
      <Switch>
        <Route path="/contact">
          <Contact />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/">
          <HomPage />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
